.profile {
    max-width: 350px;
    margin: 0 auto;
    box-shadow: 1px 0px 20px 5px rgba(0, 0, 0, 0.75);
    border-radius: 5px;

    background-color: whitesmoke;
}
.description {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}
.avatar {
    border-radius: 300px;
    width: 150px;
    margin-bottom: 20px;

    background-color: lightgrey;
}
.name {
    font-size: 24px;
    line-height: 1.26;
    font-weight: bold;

    margin-bottom: 10px;
}
.tag {
    font-size: 16px;
    line-height: 1.22;

    color: gray;

    margin-bottom: 10px;
}
.location {
    font-size: 16px;
    line-height: 1.22;

    color: gray;
}
.list {
    display: flex;

    border-top: 1px solid #514f5d85;
}
.label {
    display: block;
    margin-bottom: 10px;
}
.item {
    padding: 30px 10px;
    width: 116px;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: rgb(150 164 164);
}
.item:not(:last-child) {
    border-right: 1px solid #514f5d85;
}
.item:first-child {
    border-bottom-left-radius: 5px;
}
.item:last-child {
    border-bottom-right-radius: 5px;
}
.quantity {
    font-size: 16;
    font-weight: bold;
}